@font-face {
  font-family: 'kcfonts';
  src: url('/theme/assets/css/fontawesome/font/kcfonts.eot?80178222');
  src: url('/theme/assets/css/fontawesome/font/kcfonts.eot?80178222#iefix') format('embedded-opentype'),
       url('/theme/assets/css/fontawesome/font/kcfonts.woff2?80178222') format('woff2'),
       url('/theme/assets/css/fontawesome/font/kcfonts.woff?80178222') format('woff'),
       url('/theme/assets/css/fontawesome/font/kcfonts.ttf?80178222') format('truetype'),
       url('/theme/assets/css/fontawesome/font/kcfonts.svg?80178222#kcfonts') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
